import {createRouter, createWebHistory} from 'vue-router'


const routes = [
    {
        path: '/',
        name: 'home',
        // redirect: {name: 'singerConf'}
        component: () => import('../views/HomeView.vue'),
        props: {
            msg: "主页",
        },
    },
    {
        path: '/base',
        name: 'base',
        component: () => import('../views/TitleView.vue'),
        props: {
            baseMsg: "基础配置",
        },
        children: [
            {
                path: 'singerConf',
                name: 'singerConf',
                component: () => import('../views/SingerView.vue'),
                props: {msg: "歌手管理", baseMsg: "基础配置"}
            }, {
                path: 'songConf',
                name: 'songConf',
                component: () => import('../views/SongView.vue'),
                props: {msg: "歌曲管理", baseMsg: "基础配置"}

            }
        ]
    },
    {
        path: '/advanced',
        name: 'advanced',
        component: () => import('../views/TitleView.vue'),
        props: {msg: "高级配置"},
        children: [
            {
                path: 'playlistConf',
                name: 'playlistConf',
                component: () => import('../views/PlaylistView.vue'),
                props: {msg: "歌单管理", baseMsg: "高级配置"}
            }, {
                path: 'otherConf',
                name: 'otherConf',
                component: () => import('../views/OtherView.vue'),
                props: {msg: "其他", baseMsg: "高级配置"}
            }
        ]
    }

]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router
