<template>
  <div class="logo" ref="logo">
    <img class="logoPic" alt="logo" src="../../assets/image/ibm.svg">
  </div>

</template>

<script>


import Halo from "vanta/src/vanta.halo";
import * as THREE from '../../assets/js/three.min.js';

export default {
  name: "Logo",
  mounted() {
    this.logo = Halo({
      el: this.$refs.logo,
      mouseControls: true,
      touchControls: true,
      gyroControls: true,
      minHeight: 150.00,
      minWidth: 150.00,
      amplitudeFactor: 2.30,
      xOffset: -0.02,
      yOffset: -0.18,
      size: 1.20,
      THREE: THREE
    })
  },

  // beforeDestroy() {
  //   if (this.logo) {
  //     this.logo.destroy()
  //   }
  // }
}
</script>

<style scoped>

img {
  width: 100%;
  height: 100%;
}

.logo {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.logoPic {
  position: relative;
  width: 200px;
  margin-left: 30px;
}
</style>